html {
  box-sizing: border-box;
  font-size: 16px;
}
@media all and (min-height: 700px) {
  html {
    font-size: 19px;
  }
}
*, *:before, *:after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
::placeholder {
  color: #b6b6b6;
}
